
.plateau {
  display: flex;
  flex-direction: column-reverse;
}

#logo {
  width: 50%;
  height: 50%;
}

.link {
  font-size: 20px;
}

.ligne {
  background-color: orange;
  width: 65%;
  height: 40px;
  border-radius: 40px;
  align-items: center;
}

.littleligne {
  background-color: orange;
  width: 25%;
  height: 40px;
  border-radius: 30px;
  align-items: center !important;
}
.littleligne > div {
  height: 50%;
  display: flex;
  align-items: center;
}
.rond {
  background-color: white;
  height: 35px;
  width: 35px;
}

.rond2 {
  background-color: white;
  height: 15px;
  width: 15px;
}

.margin {
  padding-top: 1.5%;
}

.rond5, .rond6, .rond7, .rond8 {
  background-color: white;
  border-radius: 30px;
  height: 15px;
  width: 20%;
}

.violet {
  background-color: violet;
  width: 37px;
  height: 37px;
}

.grey {
  background-color: grey;
  width: 37px;
  height: 37px;
}

.blue {
  background-color: blue;
  width: 37px;
  height: 37px;
}

.black {
  background: black;
}

.gray {
  background: grey;
}

.yellow {
  background-color: yellow;
  width: 37px;
  height: 37px;
}

.red {
  background-color: red;
  width: 37px;
  height: 37px;
}

.green {
  background-color: green;
  width: 37px;
  height: 37px;
}

footer {
  margin-top: 30px;
  background-color: lightcoral;
  padding: 20px;
}
.loose {
  display: none;
}

.win {
  display: flex;
  text-align: center;
  font-size: 16px;
  color: red;
}

.sup {
  display: none;
}

.modal {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, .2);
  top: 0;
  justify-content: center;
  align-items: center;
}

#back {
  background: white;
  width: 50%;
  justify-content: center;
}


@media screen and (min-width: 1024px) {
  .win {
    font-size: 30px;
  }
  #logo {
    height: 130px;
  }

  .pause {
    margin-left: 280px;
  }
  .re {
    display: flex;
    justify-content: center;
  }
  button {
    border-radius: 30px;
    background: crimson;
    color: white;
  }
}

.bg {
background: turquoise;
}

@media screen and (min-width: 1280px) {
  .win {
    font-size: 50px;
  }
  #logo {
    height: 25%;
    width: 25%;
  }

  .ligne {
    width: 40%;
  }

  .littleligne {
    width: 30%;
  }

  .rond3, .rond4, .rond5, .rond6, .rond7, .rond8 {
    height: 60px;
    width: 60px;
    margin-left: 7px;
  }
}







